<template>
  <div class="detail_page">
   <div v-wechat-title="$route.meta.title"></div>
    <div class="detail_card">
      <div class="detail_top">
        <div class="detail_name">姓名：{{dataInfo.BDUSERNAME}}</div>
        <div class="detail_phone">电话：{{dataInfo.BDMOBILENO}}</div>
        <div class="detail_img"><img v-bind:src="dataInfo.ShopPicture" alt=""></div>
      </div>
      <div class="detail_content">
        <div class="cell_box_4_content">
          <div class="cell_box_4" v-for="(item3,index) in dataInfo.BDIncome">
            <div class="cell_4_title">
              <div class="cell_4_title_top">
                <div class="cell_4_name">123</div>
                <div class="cell_4_no">123</div>
              </div>
              <div class="cell_4_title_bottom">
                <div class="cell_4_name">123</div>
                <div class="cell_4_no">123</div>
              </div>
            </div>
          </div>
          <div class="total">
            <div class="total_left"></div>
            <div class="total_num">共123元</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
var qs = require('qs')
export default {
  data () {
    return {
      msg: 'detail',
      userInfo: {},
      dataInfo: {}
    }
  },
  created () {
    // 组件创建完后获取数据，
    // 此时 data 已经被 observed 了
    this.fetchData()
    var that = this
    var userinfo = localStorage.getItem('LOCAL_USER')
    if (userinfo) {
      that.userInfo = JSON.parse(userinfo)
    } else {
      that.userInfo = this.$store.state.userInfo
    }
  },
  watch: {
    // 如果路由有变化，会再次执行该方法
    '$route': 'fetchData'
  },
  methods: {
    fetchData () {
      var that = this
      var userinfo = localStorage.getItem('LOCAL_USER')
      if (userinfo) {
        that.userInfo = JSON.parse(userinfo)
      } else {
        that.userInfo = this.$store.state.userInfo
      }
      var detailObj = {
        Token: that.userInfo.Token,
        serviceNo: 'h5',
        userId: that.userInfo.BDUSERID,
        customerId: that.$route.params.userId,
        pageSize: 2,
        currentPage: 1
      }
      var detailUrl = '/website/app/invoker/BDIncomeService/getUserIncomeMessageByUserIdAndBDUserId'
      this.$http.post(detailUrl, qs.stringify(detailObj))
        .then(function (data) {
          that.dataInfo = data.data.responseContent
        })
    }
  }
}

</script>
<style scoped>
.detail_page {
  height: 96%;
  width: 100%;
  padding: 2% 0;
  background-color: #fff;
}

.detail_card {
  background-color: #f0f0f0;
  margin: 0 0.5rem;
  display: flex;
  height: 100%;
  flex-direction: column;
  border-radius: 1rem;
}

.detail_top {
  height: 4rem;
  display: flex;
  justify-content: space-around;
  background-color: #19c378;
  border-top-right-radius: 1rem;
  border-top-left-radius: 1rem;
  align-items: center;
  color: #fff;
  font-size: 14px;
}

.detail_content {
  flex: 1;
}

.cell_4_title {
  padding: 0 2rem;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #fff;
  height: 4rem;
}

.cell_4_name {
  height: 2rem;
  line-height: 2rem;
  font-size: 14px;
}

.cell_4_no {
  height: 2rem;
  line-height: 2rem;
  font-size: 12px;
  color: #aaa;
}

.cell_box_4_content {
  height: 100%;
}

.cell_box_4 {
  position: relative;
}

.total {
  display: flex;
  position: absolute;
  bottom: 3%;
  right: 0;
  padding: 0 0.5rem;
  width: 100%;
  justify-content: space-between;
  border-top: 1px solid #fff;
}

.total_num {
  height: 3rem;
  width: 6rem;
  display: flex;
  background-color: red;
  color: #fff;
  justify-content: center;
  align-items: center;
  border-bottom-right-radius: 1rem;
}

</style>



