import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/pages/Hello'
import Login from '@/pages/Login'
import Index from '@/pages/Index'
import Detail from '@/pages/Detail'
import Invitepartners from '@/pages/Invitepartners'
import Inviteriders from '@/pages/Inviteriders'
import Inviteusers from '@/pages/Inviteusers'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Hello',
      component: Hello
    },
    {
      path: '/Index',
      name: 'Index',
      component: Index
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/Detail',
      name: 'Detail',
      component: Detail
    },
    {
      path: '/Invitepartners',
      meta: {
        title: '邀请合伙人'
      },
      component: Invitepartners
    },
    {
      path: '/Inviteriders',
      name: 'Inviteriders',
      meta: {
        title: '邀请骑手'
      },
      component: Inviteriders
    },
    {
      path: '/Inviteusers',
      name: 'Inviteusers',
      meta: {
        title: '商户邀请'
      },
      component: Inviteusers
    }
  ]
})



