export default {
  // 保存用户信息
  SAVE_USER (state, data) {
    console.log(data)
    state.userInfo = data
  },
  SAVE_ORDER_1 (state, data) {
    state.order_1 = data
  },
  SAVE_ORDER_2 (state, data) {
    state.order_2 = data
  },
  SAVE_INCOME (state, data) {
    state.income = data
  }
}



