// 获取主机地址
import http from 'axios'
var qs = require('qs')
export default {
  login ({ commit }, loginObj) {
    var loginUrl = '/website/app/invoker/BDLogin/auth'
    return http.post(loginUrl, qs.stringify(loginObj))
      .then(function (data) {
        // 注册成功->保存到vuex的userInfo
        if (data.data.responseCode) {
          return data
        } else {
          localStorage.setItem('LOCAL_USER', JSON.stringify(data.data.responseContent))
          commit('SAVE_USER', data.data.responseContent)
          return data
        }
      })
  },
  order ({ commit }, orderObj) {
    var orderUrl = '/website/app/invoker/BDUserService/getUserOrderNumberByUserType'
    return http.post(orderUrl, qs.stringify(orderObj))
      .then(function (data) {
        if (orderObj.userType === 1) {
          commit('SAVE_ORDER_1', data.data.responseContent)
        }
        if (orderObj.userType === 2) {
          commit('SAVE_ORDER_2', data.data.responseContent)
        }
        return data
      })
  },
  income ({ commit }, incomeObj) {
    var incomeUrl = '/website/app/invoker/BDIncomeService/getUserIncomeMessageByBDUserId'
    return http.post(incomeUrl, qs.stringify(incomeObj))
      .then(function (data) {
        commit('SAVE_INCOME', data.data.responseContent)
        return data
      })
  }

}



