<template>
  <div class="users_page">
    <div v-wechat-title="$route.meta.title"></div>
    <div class="users_img"><img src="../assets/tu.png" alt=""></div>
    <div class="users_content">
      <ul>
        <li>
          <div class="users_left">手机号：</div>
          <div class="users_right">
            <div class="input">
              <input v-model="datainfo.userMobileNo" class="users_phone" type="text" placeholder="请输入手机号">
            </div>
          </div>
        </li>
        <li>
          <div class="users_left">验证码：</div>
          <div class="users_right">
            <div class="input_code">
              <input v-model="datainfo.userPwd" class="users_code" type="text" placeholder="验证码">
            </div>
            <div class="btn_code" @click="getCode()" :disabled="!show">
              <span v-show="show">发送验证码</span>
              <span v-show="!show">{{count}}s</span>
            </div>
          </div>
        </li>
        <li>
          <div @click="userReg()" class="users_btn">注册成为互联速递用户</div>
        </li>
      </ul>
    </div>
    <div class="users_footer" @click="userDownload()">
      <div class="download_btn">下载互联速递App</div>
      <p>深圳市互联速递信息科技有限公司</p>
    </div>
  </div>
</template>
<script>
const TIME_COUNT = 60
const codeUrl = '/website/app/invoker/htmlSendCode/auth'
const regUrl = '/website/app/invoker/inviteRegest/auth'
var qs = require('qs')
import { Toast } from 'mint-ui'
export default {
  data () {
    return {
      show: true,
      count: '',
      timer: null,
      datainfo: {
        userMobileNo: '',
        BDCODE: '',
        userType: '1',
        userPwd: ''
      }
    }
  },
  created () {
    // 组件创建完后获取数据，
    // 此时 data 已经被 observed 了
    this.fetchData()
  },
  watch: {
    // 如果路由有变化，会再次执行该方法
    '$route': 'fetchData'
  },
  methods: {
    userDownload () {
      window.location = 'http://appurl.me/16553623'
    },
    fetchData () {
      console.log(this.$route.query.BDCODE)
      this.datainfo.BDCODE = this.$route.query.BDCODE
    },
    userReg () {
      var that = this
      var phone = /^1\d{10}$/
      if (that.timer && phone.test(that.datainfo.userMobileNo) && that.datainfo.userPwd) {
        that.$http.post(regUrl, qs.stringify(that.datainfo))
          .then(function (data) {
            if (data.data.responseCode) {
              Toast({
                message: data.data.responseMessage,
                position: 'bottom',
                duration: 2000
              })
            } else {
              Toast({
                message: data.data.responseMessage,
                position: 'bottom',
                duration: 2000
              })
            }
          })
      } else {
        Toast({
          message: '请正确填写注册信息！',
          position: 'bottom',
          duration: 2000
        })
      }
    },
    getCode () {
      var that = this
      // 定义正则表达式
      var phone = /^1\d{10}$/
      console.log(!that.timer)
      if (phone.test(that.datainfo.userMobileNo) && !that.timer) {
        that.$http.post(codeUrl, qs.stringify(that.datainfo))
          .then(function (data) {
            if (data.data.responseCode) {
              Toast({
                message: data.data.responseMessage,
                position: 'bottom',
                duration: 2000
              })
            } else {
              Toast({
                message: data.data.responseMessage,
                position: 'bottom',
                duration: 2000
              })
              if (!that.timer) {
                that.count = TIME_COUNT
                that.show = false
                that.timer = setInterval(() => {
                  if (that.count > 0 && that.count <= TIME_COUNT) {
                    that.count--
                  } else {
                    that.show = true
                    clearInterval(that.timer)
                    that.timer = null
                  }
                }, 1000)
              }
            }
          })
      } else if (!that.timer) {
        Toast({
          message: '请输入正确手机号',
          position: 'bottom',
          duration: 2000
        })
      }
    }
  }
}

</script>
<style scoped>
.users_page{
  height: 100%;
  display: flex;
  flex-direction: column;
}
.users_img{
  height: 24rem;
}
.users_img img{
  height: 24rem;
  width: 100%;
}
.users_content{
  flex: 1;
}
.users_content ul{
  padding: 2rem;
}
.users_content ul li{
  height: 4rem;
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.users_left{
  width: 5rem;
  display: flex;
  align-items: center;
}
.users_right{
  display: flex;
}
.input input{
  width: 22rem;
  height: 3rem;
  border:1px solid #ccc;
  border-radius: 0.5rem;
}

.input_code input{
  width: 12rem;
  height: 3rem;
  border:1px solid #ccc;
  border-radius: 0.5rem;
}
.btn_code{
  width: 8rem;
  height: 3.2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background:  #00b163;
  color: #fff;
  border-radius: 0.5rem;
  margin-left:2rem;
}
.users_btn{
  background-color: #00b163;
  width: 100%;
  border-radius: 0.5rem;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}
.users_footer{
  height: 6rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 0 2rem;
}
.users_footer p{
  height: 2rem;
  line-height: 2rem;
}
.download_btn{
  height: 3rem;
  width: 100%;
  border: 1px solid #00b163;
  border-radius: 0.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #00b163;
}
</style>



