<template>
  <div class="index_page">
   <div v-wechat-title="$route.meta.title"></div>
    <div class="nav" @click="dispatching()">
      <mt-navbar v-model="selected">
        <mt-tab-item id="1">合伙人管理</mt-tab-item>
        <mt-tab-item id="2">商户管理</mt-tab-item>
        <mt-tab-item id="3">配送员管理</mt-tab-item>
        <mt-tab-item id="4">收入</mt-tab-item>
      </mt-navbar>
    </div>
    <!-- tab-container -->
    <div class="content">
    <mt-tab-container v-model="selected">
      <mt-tab-container-item id="1">
         <div class="cell_box">
          <div class="cell_title">
            <div class="cell_title_top">
              <div class="cell_name">1</div>
              <div class="cell_to">2</div>
            </div>
            <div class="cell_title_bottom">
              <div class="cell_name">3</div>
              <div class="cell_to">4</div>
            </div>
          </div>
          </div>
      </mt-tab-container-item>
      <mt-tab-container-item id="2">
        <div class="cell_box" v-for="(item1,index) in order_1.orderNumberMessage" @click="detail(item1.userId)">
          <div class="cell_img">
            <img class="cell_img_sty" src="../assets/logo@512.png" alt="">
          </div>
          <div class="cell_title">
            <div class="cell_title_top">
              <div class="cell_name">{{item1.shopName}}</div>
              <div class="cell_to">今日：{{item1.dayNumber}}单</div>
              <div class="cell_week">本周：{{item1.weekNumber}}单</div>
            </div>
            <div class="cell_title_bottom">
              <div class="cell_name">{{item1.userMobileNo}}</div>
              <div class="cell_to">本月：{{item1.mouthNumber}}单</div>
              <div class="cell_week">总计：{{item1.allNumber}}单</div>
            </div>
          </div>
          <div class="cell_status">
            <span v-bind:class="item1.STATUS=='0'?'red':'green'"></span>
          </div>
        </div>
        
      </mt-tab-container-item>
      <mt-tab-container-item id="3">
        <div class="cell_box" v-for="(item2,index) in order_2.orderNumberMessage" @click="detail(item2.userId)">
          <div class="cell_title">
            <div class="cell_title_top">
              <div class="cell_name">{{item2.shopName}}</div>
              <div class="cell_to">今日：{{item2.dayNumber}}单</div>
              <div class="cell_week">本周：{{item2.weekNumber}}单</div>
            </div>
            <div class="cell_title_bottom">
              <div class="cell_name">{{item2.userMobileNo}}</div>
              <div class="cell_to">本月：{{item2.mouthNumber}}单</div>
              <div class="cell_week">总计：{{item2.allNumber}}单</div>
            </div>
          </div>
          <div class="cell_status">
            <span v-bind:class="item2.STATUS=='0'?'red':'green'"></span>
          </div>
        </div>
      </mt-tab-container-item>
      <mt-tab-container-item id="4">
        <div class="cell_box_4_content">
       <div class="cell_box_4" v-for="(item3,index) in income.BDIncome">
         <div class="cell_4_title">
           <div class="cell_4_title_top">
             <div class="cell_4_name">{{item3.INCOMEDES}}</div>
             <div class="cell_4_no">{{item3.CREATETIME}}</div>
           </div>
           <div class="cell_4_title_bottom">
             <div class="cell_4_name">+{{item3.MONEY}}元</div>
             <div class="cell_4_no">{{item3.ORDERID}}</div>
           </div>
         </div>
       </div>
       <div class="total">
           <div class="total_left"></div>
           <div class="total_num">共{{income.allMoney}}元</div>
         </div>
         </div>
      </mt-tab-container-item>
    </mt-tab-container>
    </div>
  </div>
</template>
<script>
import { Navbar, TabItem } from 'mint-ui'
export default {
  data () {
    return {
      selected: '4',
      userInfo: {},
      qsOrder: {}
    }
  },
  components: {
    Navbar,
    TabItem
  },
  created () {
    // 1商户 2配送员
    var that = this
    var userinfo = localStorage.getItem('LOCAL_USER')
    if (userinfo) {
      that.userInfo = JSON.parse(userinfo)
    } else {
      that.userInfo = this.$store.state.userInfo
    }
  },
  computed: {
    order_1 () {
      return this.$store.state.order_1
    },
    order_2 () {
      return this.$store.state.order_2
    },
    income () {
      return this.$store.state.income
    }
  },
  methods: {
    dispatching () {
      var that = this
      var selectedNo = that.selected
      if (that.userInfo.Token) {
        var orderObj = {
          Token: that.userInfo.Token,
          serviceNo: 'h5',
          userId: that.userInfo.BDUSERID,
          pageSize: 2,
          currentPage: 1
        }
        var incomeObj = {
          Token: that.userInfo.Token,
          serviceNo: 'h5',
          userId: that.userInfo.BDUSERID,
          pageSize: 2,
          currentPage: 1
        }
        if (selectedNo === '4') {
          that.$store.dispatch('income', incomeObj)
        }
        if (selectedNo === '3') {
          that.orderData(orderObj, 2)
        }
        if (selectedNo === '2') {
          that.orderData(orderObj, 1)
        }
      } else {
        that.$router.push({ path: '/login' })
      }
    },
    orderData (orderObj, userType) {
      var that = this
      orderObj.userType = userType
      that.$store.dispatch('order', orderObj)
            .then(function (data) {
              if (data.data.responseCode === 0) {
                if (userType === 1) {
                  console.log(1)
                }
                if (userType === 2) {
                  console.log(2)
                }
              } else {
                that.$router.push({ path: '/login' })
              }
            })
    },
    detail (userId) {
      var that = this
      that.$router.push({name: 'Detail', params: { userId: userId }})
    }
  }
}
</script>
<style scoped>
@media (device-height:568px) and (-webkit-min-device-pixel-ratio:2) {
  .cell_title {
    padding: 0rem;
    font-size: 10px;
  }
  .cell_img_sty {
    width: 3.5rem!important;
    height: 3.5rem!important;
  }
  .cell_name {
    width: 7rem!important;
  }
  .cell_week {
    width: 5rem;
  }
}

.index_page {
  height: 96%;
  padding: 2% 0.5rem;
  display: flex;
  background-color: #fff;
  flex-direction: column;
}
.content{
  flex: 1;
  background-color: #f0f0f0;
  border-bottom-right-radius: 1rem;
  border-bottom-left-radius: 1rem;
  border:1px solid rgba(0,0,0,.0470588);
  box-shadow: rgba(0,0,0,.0470588) 0 4px 3px 0;
}
.mint-navbar {
  background-color: #19c378;
  border-top-right-radius: 1rem;
  border-top-left-radius: 1rem;
}

.mint-navbar .mint-tab-item.is-selected {
  color: #4c4c4c;
  background-color: #f0f0f0;
  border-top-right-radius: 1rem;
  border-top-left-radius: 1rem;
  border: none;
}

.mint-tab-item {
  color: #fff;
}

.mint-tab-container {
  padding: 0.5rem;
  height: 100%;
}

.cell_box {
  display: flex;
  background-color: #fff;
  padding: 0.5rem;
  border-top-right-radius: 1rem;
  border-bottom-left-radius: 1rem;
  margin-top: 0.5rem;
}

.cell_img {
  display: flex;
  justify-content: center;
  align-items: center;
}

.cell_img_sty {
  width: 4.5rem;
  height: 4.5rem;
}

.cell_title {
  padding: 0.5rem;
  font-size: 14px;
}

.cell_title_top {
  display: flex;
  height: 2rem;
  line-height: 2rem;
}

.cell_title_bottom {
  display: flex;
  height: 2rem;
  line-height: 2rem;
}

.cell_name {
  width: 9rem;
}

.cell_to {
  width: 8rem;
}

.cell_week {
  width: 8rem;
}

.cell_status {
  display: flex;
  justify-content: center;
  align-items: center;
}

.cell_status span {
  display: block;
  height: 1rem;
  width: 1rem;
  border-radius: 50%;
}
.red{
 background-color: red;
}
.green{
   background-color: green;
}
.cell_4_title{
  padding: 0 2rem;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #fff;
  height: 4rem;
}
.cell_4_name{
  height: 2rem;
  line-height: 2rem;
  font-size: 14px;
}
.cell_4_no{
  height: 2rem;
  line-height: 2rem;
  font-size: 12px;
  color: #aaa;
}
.cell_box_4_content{
  height: 100%;
}
.cell_box_4{
  position: relative;
}
.total{
  display: flex;
  position: absolute;
  bottom: 2%;
  right: 0;
  width: 100%;
  justify-content: space-between;
  border-top:1px solid #fff;
}
.total_num{
  height: 3rem;
  width: 6rem;
  display: flex;
  background-color: red;
  color: #fff;
  justify-content: center;
  align-items: center;
  border-bottom-right-radius: 1rem;
}
</style>



