<template>
  <div class="login_page">
   <div v-wechat-title="$route.meta.title"></div>
    <div class="login_logo">
      <img class="logo_img" src="../assets/logo@512.png" alt="">
    </div>
    <mt-field label="手机号" placeholder="请输入手机号" type="tel" v-model="loginObj.userMobileNo"></mt-field>
    <mt-field label="验证码" v-model="loginObj.userPwd">
      <p class="sendCode" href="">发送验证码</p>
    </mt-field>
    <mt-button size="large" type="primary" @click="login()">登录</mt-button>
  </div>
</template>
<script>
import { Toast } from 'mint-ui'
export default {
  data () {
    return {
      loginObj: {
        userMobileNo: '',
        serviceNo: 'h5',
        userPwd: ''
      }

    }
  },
  created () {},
  methods: {
    login () {
      var that = this
      this.$store.dispatch('login', that.loginObj)
        .then(function (data) {
          Toast({
            message: data.data.responseMessage,
            position: 'bottom',
            duration: 1000
          })
          if (data.data.responseCode === 0) {
            that.$router.push({ path: '/index' })
          }
        })
    }
  }
}

</script>
<style scoped>
.login_page {
  padding: 0.7rem
}

.login_logo {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 20rem;
}

.logo_img {
  height: 7rem;
  width: 7rem;
}

</style>



