<template>
  <div class="partners_page">
   <div v-wechat-title="$route.meta.title"></div>
  	<div class="part_img"><img src="../assets/tu.png" alt=""></div>
    <div class="part_content">
    	<ul>
    		<li><label for="">手机号：</label><input class="part_phone" type="text"></li>
    		<li><label for="">验证码：</label><input class="part_code" type="text"><span>发送验证码</span></li>
    		<li><label for="">姓名：</label><input class="part_phone" type="text"></li>
    		<li><label for="">省市区：</label><input class="part_phone" type="text"></li>
    		<li><label for="">站编号：</label><input class="part_phone" type="text"></li>
    	</ul>
    </div>
     <v-distpicker v-bind:hidden="hidden"  type="mobile"  @selected="onSelected"></v-distpicker>
  </div>
</template>
<script>
import VDistpicker from 'v-distpicker'
export default {
  data () {
    return {
      province: '',
      city: '',
      area: '',
      hidden: false
    }
  },
  components: {
    VDistpicker
  },
  methods: {
    onSelected (data) {
      console.log(data.province)
      console.log(data.city)
      console.log(data.area)
    }
  }
}

</script>
<style>
.part_content ul{
	padding: 2rem;
}
.part_content ul li{
	padding-top: 0.5rem;
	display: flex;
	align-items: center;
	}
.part_content ul li label{
	display: block;
	width: 20%;
}	
.part_phone{
	width: 70%;
	height: 2rem;
	border-radius: 0.5rem;
	border: 1px solid #ccc;
}
.part_code{
	width: 50%;
	height: 2rem;
	border-radius: 0.5rem;
	border: 1px solid #ccc;
}
.address{
	height: 20rem;
	position: fixed;
	bottom: 0;
	left: 0;
	width: 100%;
}
.address-container{
	height: 20rem!important;
}
.address-container ul{
	overflow-y:scroll;
	}
.partners_page {
  width: 100%;
}

.part_img {
  width: 100%;
  height: 24rem;
}

.part_img img {
  height: 24rem;
  width: 100%;
}

.part_content {}

</style>



